import React from "react";
import FilledBtn from "../Buttons/FilledBtn";
import "./profilemodal.style.css";

const ProfileModal = ({ setModal }) => {
  const closeModal = () => {
    setModal(false);
    document.body.style.overflowY = "auto";
  };
  return (
    <>
      <div className="overlay__background" onClick={closeModal}></div>
      <div className="profile__modal">
        <h1>Ajouter un utilisateur</h1>
        <div className="select__area">
          <h4>L'état du projet</h4>
          <select name="status" className="select">
            <option value="Examiné">Examiné</option>
            <option value="En cours">En cours</option>
            <option value="Finalisé">Finalisé</option>
          </select>
        </div>
        <button className="submit">Mettre à jour</button>
      </div>
    </>
  );
};

export default ProfileModal;
