import React, { useState } from "react";
import "./administrationmodal.style.css";
import Select from "react-select";

const options = [
  { value: "chocolate", label: "Chocolate" },
  { value: "strawberry", label: "Strawberry" },
  { value: "vanilla", label: "Vanilla" },
];

const AdministrationModal = ({ setModal }) => {
  const [selectedOptions, setSelectedOptions] = useState([]);
  const [disabled, setDisabled] = useState(true);
  // const [confirmationModal, setConfirmationModal] = useState(initialState)
  const closeModal = () => {
    setModal(false);
    document.body.style.overflowY = "auto";
  };
  const handleChange = (values) => {
    setSelectedOptions(values);
  };

  return (
    <>
      <div className="overlay__background" onClick={closeModal}></div>
      <div className="admin__modal">
        <h1>Créer un sous-administrateur</h1>
        <div className="select__area">
          <h4>Nom de l'utilisateur</h4>
          <Select isMulti onChange={handleChange} options={options} />
        </div>
        <div className="check__box__area">
          <h4>Modifier l'accès</h4>
          <ul class="unstyled centered">
            <li>
              <input
                class="styled-checkbox"
                id="styled-checkbox-1"
                type="checkbox"
                value="value1"
              />
              <label for="styled-checkbox-1">Approuver les modifications</label>
            </li>
          </ul>
        </div>
        <button
          className={!selectedOptions.length > 0 ? `submit disabled` : `submit`}
          disabled={!selectedOptions.length > 0 ? true : false}
        >
          Add
        </button>
      </div>
    </>
  );
};

export default AdministrationModal;
