import React, { useState } from "react";
import "./detailedprofile.style.css";

// icons and images
import Profile3 from "../../../Assets/Images/Profile3.png";

// data
import { data } from "./data";

// components
import ProfileModal from "../../Modals/ProfileModal";
import ProfileTable from "../../Tables/ProfileTable";
import EmptyLinkBtn from "../../Buttons/EmptyLinkBtn";

const Table = () => {
  const [menu, setMenu] = useState(null);
  const [modal, setModal] = useState(false);

  const handleMenu = (id) => {
    if (id === menu) setMenu(null);
    else setMenu(id);
  };

  const openModal = () => {
    setModal(true);
    document.body.style.overflowY = "hidden";
  };

  return (
    <>
      {modal && <ProfileModal setModal={setModal} />}
      <div className="constructor__details">
        <div className="constructor__profile">
          <img src={Profile3} alt="profile" />
          <div className="data">
            <h2>John Doe</h2>
            <h4>kimbe2@gmail.com</h4>
          </div>
        </div>
        <div>
          <button className="danger">Effecer</button>
          <EmptyLinkBtn
            path="/assignProject"
            size="medium"
            title="Attribuer un projet"
          />
        </div>
        <ProfileTable
          handleMenu={handleMenu}
          openModal={openModal}
          data={data}
          menu={menu}
        />
      </div>
    </>
  );
};

export default Table;
