import React, { useState } from "react";
import { useHistory } from "react-router-dom";

// style
import "./signin.style.css";

// icons
import { AiFillEye, AiFillEyeInvisible } from "react-icons/ai";

// validations
import { useFormik } from "formik";
import * as Yup from "yup";

const Form = () => {
  const history = useHistory();
  const [showPassword, setShowPassword] = useState(false);
  const initialValues = {
    email: "",
    password: "",
  };

  const validationSchema = Yup.object({
    email: Yup.string()
      .email("Invalid email format")
      .required("Email is required"),
    password: Yup.string().required("Password is required"),
  });

  const onSubmit = () => {
    // alert(formik.values.email + "," + formik.values.password);
    history.push("/dashboard");
  };

  const formik = useFormik({
    initialValues,
    onSubmit,
    validationSchema,
  });

  return (
    <form onSubmit={formik.handleSubmit}>
      <div className="email__field">
        <label htmlFor="email">Nom d'utilisateur</label>
        <br />
        <input
          type="email"
          placeholder="Mzaka113@gmail.com"
          id="email"
          name="email"
          {...formik.getFieldProps("email")}
        />
        {formik.touched.email && formik.errors.email ? (
          <div id="error">{formik.errors.email}</div>
        ) : null}
      </div>
      <div className="password__field">
        <label htmlFor="password">Mot de passe</label>
        <br />
        <div className="password__input__area">
          <input
            type={showPassword ? "text" : "password"}
            placeholder="8 + personnages"
            id="password"
            name="password"
            {...formik.getFieldProps("password")}
          />
          <div
            className="password__icon"
            onClick={() => setShowPassword(!showPassword)}
          >
            {!showPassword ? <AiFillEye /> : <AiFillEyeInvisible />}
          </div>
        </div>

        {formik.touched.password && formik.errors.password ? (
          <div id="error">{formik.errors.password}</div>
        ) : null}
      </div>
      <button id="signin__btn" type="submit">
        Connexion
      </button>
    </form>
  );
};

export default Form;
